import * as Type from './types'
import api from './api'
import * as apiTools from '../../utils/api'
import * as GlobalType from '@/store/global/types'

const actions = {
  [Type.A_START_MAIN]: ({commit, state, dispatch}, payload) => {
    dispatch(Type.A_ZONE_INFO, {userId: payload.userId}).then(() => {
      console.log("",window.initState.userId === payload.userId)
      if(window.initState.userId === payload.userId){
        dispatch(Type.A_MINE_PUBLISH)
      }
    })
  },
  [Type.A_ZONE_INFO]: ({commit, state, dispatch}, payload) => {
    console.log("", payload)
    return apiTools.req(api.userInfo, payload, rsp => {
      commit(Type.M_ZONE_INFO_SUCCESS, rsp)
    }, msg => {
      commit(Type.M_ZONE_INFO_ERROR, msg)
    })
  },
  [Type.A_SEND_MSG]: ({commit, state, dispatch}, payload) => {
    console.log("", payload)
    return apiTools.post(api.send, payload, rsp => {
      commit(Type.M_SEND_MSG_SUCCESS, rsp)
    }, msg => {
      commit(Type.M_SEND_MSG_ERROR, msg)
    })
  },
  [Type.A_MINE_PUBLISH]: ({commit, state, dispatch}, payload) => {
    return apiTools.req(api.mine, payload, rsp => {
      console.log("")
      commit(Type.M_MINE_PUBLISH, rsp)
    }, msg => {
      console.log("", msg)
    })
  },
  [Type.A_GET_PUBLISH]: ({commit, state, dispatch}, payload) => {
    return apiTools.req(api.info, payload, rsp => {
      commit(Type.M_GET_PUBLISH, rsp)
    }, msg => {
      console.log("", msg)
    })
  },
  [Type.A_MINE_TOPIC]: ({commit, state, dispatch}, payload) => {
    return apiTools.req(api.info, payload, rsp => {
      commit(Type.M_MINE_TOPIC, rsp)
    }, msg => {
      console.log("", msg)
    })
  },
  [Type.A_MINE_WARN]: ({commit, state, dispatch}, payload) => {
    return apiTools.req(api.info, payload, rsp => {
      commit(Type.M_MINE_WARN, rsp)
    }, msg => {
      console.log("", msg)
    })
  },
  [Type.A_UPDATE_MINE_PUBLISH]: ({commit, state, dispatch}, payload) => {
    return apiTools.post(api.mineUpdate, payload, rsp => {
      commit(Type.M_UPDATE_MINE_SUCCESS, rsp)
    }, msg => {
      commit(Type.M_UPDATE_MINE_ERROR,msg)
    })
  },
  [Type.A_UPDATE_OTHER_PUBLISH]: ({commit, state, dispatch}, payload) => {
    return apiTools.post(api.otherUpdate, payload, rsp => {
      commit(Type.M_UPDATE_OTHER_SUCCESS, rsp)
    }, msg => {
      commit(Type.M_UPDATE_OTHER_ERROR,msg)
    })
  },


}

export default actions



