<template>
    <div class="my-info-box" v-if="data&&data.taskInfo" @click="goTaskInfo(data.taskInfo.taskId)">
        <div class="info-box-hd ">
            <h4 class="info-box_title my-ellipsis">
                {{data.taskInfo.title}}
            </h4>
            <p></p>
        </div>
        <p class="info-box_info">{{data.taskInfo.money}}</p>
        <div class="my-space-Between">
            <p class="info-box_info">{{data.taskInfo.address}}</p>
            <el-button-group v-if="visible " class="btn-group">
                <!--<el-button type="primary"></el-button>-->
                <el-button v-if="data.taskInfo.stu ===4 || data.taskInfo.stu ===8||data.taskInfo.stu ===12" type="primary" @click.stop="ok"></el-button>
                <el-button v-if="data.taskInfo.stu ===1 || data.taskInfo.stu ===4||data.taskInfo.stu ===12" type="primary" @click.stop="otherOk(2)"></el-button>
                <el-button v-if="data.taskInfo.stu ===5 || data.taskInfo.stu ===13" type="primary" @click.stop="otherOk(3)"></el-button>
                <el-button v-if="data.taskInfo.stu ===1 || data.taskInfo.stu ===3||data.taskInfo.stu ===7||data.taskInfo.stu ===9||data.taskInfo.stu ===11" type="primary" @click.stop="otherOk(1)"></el-button>
            </el-button-group>
        </div>
        <p class="info-box_info">{{data.taskInfo.deadTime}} </p>
        <p class="info-box_info">{{data.taskInfo.status}}</p>
        <p v-if="data.taskFinish" class="info-box_info">{{data.taskFinish.taker.userName}}</p>
        <div class="my-space-Between">
            <div class="info-box_tags my-ellipsis" v-if="data.taskInfo.tags">
                <el-tag color="#6992cc" v-for="item in data.taskInfo.tags" key>{{item.name}}</el-tag>
            </div>
            <div v-else>
                <el-tag type="gray"></el-tag>
            </div>
            <span class="info-box_info info-box_time">{{data.time}}</span>
        </div>

    </div>
</template>
<script>
  import {mapActions, mapState} from  'vuex'
  import * as ZoneType from '@/store/zone/types'
  export default{
    name: 'infoBox',
    props: {
      visible: {
        type: Boolean,
        default: false
      },
      data: {
        type: Object,
        default(){
          return {
            taskInfo: {
              senderInfo: {
                id: 1,
                avatarId: 0,
                name: "",
                userName: "timebank"
              },
              finisherInfo: {
                id: 2,
                avatarId: 5,
                name: "",
                userName: "long"
              },
              taskId: 1,
              title: "",
              description: "",
              remark: "",
              demand: "",
              urgent: 1,
              urgentMoney: 5,
              serviceTime: "",
              deadTime: "2017-05-23 08:44:32",
              address: "",
              personal: "",
              money: 10,
              tagIds: null,
              tags: [
                {
                  id: 1,
                  name: "",
                  createTime: "2017-05-22 08:45:43",
                  modifyTime: "2017-05-22 08:45:43"
                }
              ],
              status: ",",
              stu: 4
            },
            taskFinish: {
              finishId: 1,
              finishTime: null,
              remark: "",
              createTime: "2017-05-22 08:45:43",
              stu: 4,
              status: ",",
              taskId: 1,
              sender: {
                id: 1,
                avatarId: 0,
                name: "",
                userName: "timebank"
              },
              taker: {
                id: 2,
                avatarId: 5,
                name: "",
                userName: "long"
              }
            }
          }
        }
      }

    },
    computed: {
      ...mapState({
        mineUpdateStep: state => state.zone.mineUpdateStep,
        mineUpdateError: state => state.zone.mineUpdateError,
        otherUpdateStep: state => state.zone.otherUpdateStep,
        otherUpdateError: state => state.zone.otherUpdateError,

      })
    },
    methods: {
      ...mapActions({
       updateMinePublish : ZoneType.A_UPDATE_MINE_PUBLISH,
       updateOtherPublish : ZoneType.A_UPDATE_OTHER_PUBLISH,
       minePublish : ZoneType.A_MINE_PUBLISH,
      }),
      ok(){

        let self = this
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          console.log("taskId",self.data.taskInfo.taskIdd)
          self.updateMinePublish({taskId:self.data.taskInfo.taskId}).then(()=>{
            if(self.mineUpdateStep === 'error'){
              self.$message.error(self.mineUpdateError)
            }else{
              self.minePublish()
              self.$message.success("")
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      otherOk(type){
        let self = this
        let msg = ''
        if(type === 1){
          msg = ""
        }else if(type === 2){
          msg = ""
        }else if(type === 3){
          msg = ""
        }
        this.$confirm(msg, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          console.log("taskId",)
          self.updateOtherPublish({taskId:self.data.taskInfo.taskId,type:type}).then(()=>{
            if(self.otherUpdateStep === 'error'){
              self.$message.error(self.otherUpdateError)
            }else{
              self.minePublish()
              self.$message.success("")
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      goTaskInfo(taskId) {
        this.GM_routerPush({
          path: '/intro',
          query: {
            taskId: taskId
          }
        })
      }
    }
  }
</script>



